<template>
	<div class="signup container">
		<form class="card-panel" @submit.prevent="signup">
			<h2 class="center deep-purple-text">Signup</h2>
			<div class="field">
				<label for="email">Почта</label>
				<input id="email" type="email" v-model="email">
			</div>
			<div class="field">
				<label for="password">Пароль</label>
				<input id="password" type="password" v-model="password">
			</div>

			<div class="field">
				<label for="selected">Имя</label>
				<select class="browser-default" value="" id="selected" v-model="selected">
					<option v-for="option in participants" v-bind:value="option.slug">
						{{ option.name }}
					</option>

				</select>
			</div>
			<p class="red-text center" v-if="feedback">{{ feedback }}</p>
			<div class="field center">
				<button class="btn deep-purple">Signup</button>
			</div>
		</form>
	</div>
</template>

<script>
	import slugify from 'slugify'
	import db from '@/firebase/init'
	import firebase from 'firebase'


	export default {
		name: 'Signup',
		data() {
			return {
				email: null,
				password: null,
				feedback: null,
				selected: null,
				slug: null,
				participants: []
			}
		},
		beforeMount() {
			db.collection('participants').get().then(participant => {
				participant.docs.forEach(doc => {
					let data = doc.data();
					data.slug = doc.id;
					if(!data.give) {
						this.participants.unshift(data)
					}
				})
			}).catch(err => {
				console.log(err);
			})
		},

		methods: {
			signup() {

				if(this.email && this.password && this.selected) {
					this.slug = slugify(this.selected, {
						replacement: '-',
						remove: /[$*_+~.()'"!\-:@]/g,
						lower: true
					})

					let ref = db.collection('users').doc(this.slug)

					ref.get().then(doc => {
						if(doc.exists) {
							this.feedback = 'This alias already exists'
						} else {
							firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
								.then(cred => {
									ref.set({
										alias: this.selected,
										selected_id: this.slug,
										user_id: cred.user.uid
									})
								})
								.then(() => {
									db.collection('participants').doc(this.selected).update({
										give: true
									})
									this.$router.push({name: 'Index'})
								})
								.catch(err => {
									console.log(err);
									this.feedback = err.message
								})
							this.feedback = 'This alias is free to use'
						}
					})

				} else {
					this.feedback = 'You must enter all fields'
				}
			}
		}
	}
</script>

<style>
	.signup {
		max-width: 400px;
		margin-top: 60px;
	}

	.signup h2 {
		font-size: 2.4em;
	}

	.signup .field {
		margin-bottom: 16px;
	}

	.signup .browser-default {
		border: none;
		border-bottom: 1px solid #9e9e9e;
		border-radius: 0;
		outline: none;
		height: 3rem;
		width: 100%;
		font-size: 16px;
		margin: 0 0 8px 0;
		padding: 0;
	}
</style>




