<template>
	<transition name="modal">
		<div class="modal-mask">
			<div class="modal-wrapper">
				<div class="modal-container">

					<div class="modal-header">
						<slot name="header">
							default header
						</slot>
					</div>

					<div class="modal-body">
						<slot name="body">
							default body
						</slot>
					</div>

					<div class="modal-footer">
						<slot name="footer">
						</slot>
					</div>

					<div class="modal-default-button" @click="$emit('close')"></div>

				</div>
			</div>
		</div>
	</transition>
</template>

<script>
	export default {
		name: "AppModal"
	}
</script>

<style>
	.modal-mask {
		position: fixed;
		z-index: 9998;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, .5);
		display: table;
		transition: opacity .3s ease;
	}

	.modal-wrapper {
		display: table-cell;
		vertical-align: middle;
		position: relative;
	}

	.modal-container {
		position: relative;
		max-width: 600px;
		width: 100%;
		margin: 0px auto;
		padding: 20px 30px;
		background-color: #fff;
		border-radius: 2px;
		box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
		transition: all .3s ease;
		font-family: Helvetica, Arial, sans-serif;
	}

	.modal-header h3 {
		font-size: 34px;
		text-align: center;
		margin-top: 0;
		color: #000;
		margin-bottom: 40px;
	}

	.modal-body {
		font-size: 24px;
		text-align: center;
		margin: 20px 0;
	}

	.modal-default-button {
		/*float: right;*/
		position: absolute;
		width: 23px;
		height: 23px;
		cursor: pointer;
		top: 19px;
		right: 19px;
	}
	.modal-default-button:after, .modal-default-button:before {
		width: 23px;
		height: 1px;
		margin-top: 0;
		margin-left: -11px;
		background: #003da6;
	}
	.modal-default-button:after {
		transform: rotate(-45deg);
	}
	.modal-default-button:before {
		transform: rotate(45deg);
	}
	.modal-default-button:before, .modal-default-button:after {
		content: "";
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		background: #555;
		height: 2px;
		width: 30px;
		margin-top: -2px;
		margin-left: -15px;
		border-radius: 10px;
	}
	.modal-enter {
		opacity: 0;
	}

	.modal-leave-active {
		opacity: 0;
	}

	.modal-enter .modal-container,
	.modal-leave-active .modal-container {
		-webkit-transform: scale(1.1);
		transform: scale(1.1);
	}
</style>


