import db from '@/firebase/init'
import firebase from 'firebase'
import router from '@/router'

export default {
	namespaced: true,
	state: {
		user: null,
		participants: [],
		selectedUser: null,
	},
	getters: {
		getUser: state => state.user,
		getParticipants: state => state.participants,
	},
	mutations: {
		setDisabled(state, status){

		},
		setUser(state) {
			let user = firebase.auth().currentUser,
				ref = db.collection('users');

			//get current user
			if(user) {
				ref.where('user_id', '==', user.uid).get()
					.then(snapshot => {
						snapshot.forEach(doc => {
							if(doc) {
								state.user = doc.data(),
									state.user.id = doc.id;
							} else {
								state.user = null;
							}
						})
					})
			}
		},
		setParticipants(state) {
			//get all participants
			db.collection('participants').get().then(participant => {
				participant.docs.forEach(doc => {
					let data = doc.data()
						data.slug = doc.id;
					if(doc.id != state.user.selected_id && !data.get) {
						state.participants.unshift(data);
					}
				})
			})
		},
	},
	actions: {
		SETCURRENTUSER({commit}) {
			commit('setUser')
		},
		SETPARTICIPANTS({commit}) {
			commit('setParticipants')
		}
	}
}



